<template>
  <!-- Rows -->
  <div class="row justify-content-center">
    <!-- Columns -->
    <div class="col-md-4" style="margin-top: 13%;">
      <!-- Cards -->
      <div class="card flex-grow-1">
        <!-- Card Headers -->
        <div class="card-header">
          <div class="text-center">
            <h5>Please Login to Continue</h5>
          </div>
        </div>
        <!-- Card Body -->
        <div class="card-body">
          <!-- Email Address -->
          <div class="me-3">
            <div class="form-label">Email</div>
            <!-- eslint-disable max-len -->
            <input ref="email" v-model="email" class="form-control" type="email" placeholder="example@natsu.ntx">
            <p v-if="error === -1" class="form-text text-danger fst-italic">Cannot Find that Email!</p>
            <p v-if="emailEmpty" class="form-text text-danger fst-italic">Please Type your Email!</p>
          </div>
          <!-- Password -->
          <div class="mt-3 me-3">
            <div class="form-label">Password</div>
            <!-- eslint-disable max-len -->
            <input ref="password" v-model="password" class="form-control" type="password" placeholder="Password">
            <p v-if="error === -2" class="form-text text-danger fst-italic">Wrong Password!</p>
            <p v-if="passwordEmpty" class="form-text text-danger fst-italic">Please Type Your Password!</p>
          </div>
          <!-- NIM Code -->
          <div class="mt-3 me-3">
            <div class="form-label text-uppercase">nim</div>
            <!-- eslint-disable max-len -->
            <input ref="nim-code" v-model="NIM" class="form-control" type="text" placeholder="NIM Number">
            <p v-if="error === -3" class="form-text text-danger fst-italic">Invalid NIM Number!</p>
            <p v-if="nimEmpty" class="form-text text-danger fst-italic">Please Type Your NIM Number!</p>
          </div>
        </div>
        <!-- Card Footer -->
        <div class="card-footer">
          <button @click="login()" ref="loginbtn" class="btn-primary btn" disabled>Login</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import accounts from '../accounts/accounts';

export default {
  name: 'formLogin',
  data() {
    return {
      email: '',
      password: '',
      NIM: '',
      error: 0,
      emailEmpty: false,
      passwordEmpty: false,
      nimEmpty: false,
    };
  },
  methods: {
    login() {
      this.error = 0;
      // eslint-disable-next-line no-useless-return
      if (this.emailEmpty || this.passwordEmpty || this.nimEmpty) return;
      const data = accounts.loginToDashboard(this.email, this.password, this.NIM);
      if (data.isAuth) {
        this.$store.commit('saveUserData', data);
        this.$router.push({ path: '/' });
      } else {
        // eslint-disable-next-line default-case
        switch (data.error) {
          case 'No Email':
            this.error = -1;
            break;
          case 'Wrong Password':
            this.error = -2;
            break;
          case 'Invalid NIM':
            this.error = -3;
            break;
        }
      }
    },
  },
  updated() {
    this.$nextTick(() => {
      // Email Fields is Empty
      if (this.email === '') {
        this.$refs.email.classList.add('border-danger');
        this.$refs.loginbtn.disabled = true;
        this.emailEmpty = true;
      } else {
        this.$refs.email.classList.remove('border-danger');
        this.emailEmpty = false;
      }
      // Password Fields is Empty
      if (this.password === '') {
        this.$refs.password.classList.add('border-danger');
        this.$refs.loginbtn.disabled = true;
        this.passwordEmpty = true;
      } else {
        this.$refs.password.classList.remove('border-danger');
        this.passwordEmpty = false;
      }
      // NIM Code Fields is Empty
      if (this.NIM === '') {
        this.$refs['nim-code'].classList.add('border-danger');
        this.$refs.loginbtn.disabled = true;
        this.nimEmpty = true;
      } else {
        this.$refs['nim-code'].classList.remove('border-danger');
        this.nimEmpty = false;
      }
      // Make Sure All Field has a data then enable the Login Button
      if (!this.emailEmpty && !this.passwordEmpty && !this.nimEmpty) {
        this.$refs.loginbtn.disabled = false;
      }
    });
  },
};
</script>
