import sha512 from 'crypto-js/sha512';
import { accounts } from './test.json';

export default {
  // eslint-disable-next-line consistent-return
  loginToDashboard: (email, password, NimCode) => {
    let user = '';
    let pass = '';
    let nim = '';
    let userData = {};

    // eslint-disable-next-line no-restricted-syntax
    for (const accs of accounts) {
      email.toLowerCase();
      // eslint-disable-next-line no-use-before-define
      const psw = reverse(sha512(password).toString());

      // eslint-disable-next-line no-restricted-syntax
      if (accs.email === email) {
        user = email;
      }
      if (accs.password === psw) {
        pass = psw;
      }
      if (accs.NIM === NimCode) {
        nim = NimCode;
        userData = accs.userData;
      }
    }
    if (!user) {
      return { error: 'No Email' };
    }
    if (!pass) {
      return { error: 'Wrong Password' };
    }
    if (!nim) {
      return { error: 'Invalid NIM' };
    }
    if (user && pass && nim) {
      return { isAuth: true, data: userData };
    }
  },
};

function reverse(string) {
  // Split it to Array
  const arrays = string.split('');
  // Reverse it
  const reverses = arrays.reverse();
  // Join All Character from an array
  // and return the result
  return reverses.join('');
}
